.movie {
  background-color: white;
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
}

.movie_columns {
  width: 30%;
  box-sizing: border-box;
  text-overflow: ellipsis;
}

.movie_column:last-child {
  padding: 20px 0;
  width: 60%;
}

.movie h1 {
  font-size: 20px;
  font-weight: 600;
}

.movie .movie_genres {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.movie_genres .movie_genre {
  margin-right: 10px;
  color: #b4b5bd;
}

.movie .movie_synopsis {
  font-size: 1.1em;
  color: #b4b5bd;
}

.movie .movie_Poster {
  max-width: 100%;
  position: relative;
  top: -20px;
  box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width: 320px) and (max-width: 667px) {
  .novie {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .novie {
    width: 100%;
    flex-direction: column;
  }
  .novie_poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .movie_columns {
    width: 100% !important;
  }
}
